import { Fragment } from 'react';

import {
  Clock,
  ClockFormat,
  ControlPanel,
  DynamicBackground,
  Greeting,
  InlineToDo,
  Intro,
  Quote,
  Search,
  Shortcuts,
} from '../../features';
import { useStorage } from '../../hooks';
import { namePersistedStorage, settingsPersistedStorage } from '../../storages';
import {
  StyledBottomContainer,
  StyledClockContainer,
  StyledContentContainer,
  StyledGreetingContainer,
  StyledQuoteContainer,
  StyledSearchContainer,
  StyledShortcutsContainer,
  StyledToDoContainer,
  StyledTopContainer,
} from './MainView.styles';

export function MainView() {
  const {
    data: name,
    isInitialized: isNameInitialized,
    error: nameError,
  } = useStorage({ storage: namePersistedStorage });
  const {
    data: settings,
    isInitialized: isSettingsInitialized,
    error: settingsError,
  } = useStorage({ storage: settingsPersistedStorage });

  const error = nameError || settingsError;

  if (error) {
    return <div>Could not initialize the main screen: {error}</div>;
  }

  if (
    !isSettingsInitialized ||
    !isNameInitialized ||
    // Settings have a default value, so they cannot be empty after initialization
    !settings
  ) {
    return null;
  }

  const needToRequestUserName = settings.isGreetingEnabled && settings.isGreetingWithName && !name;

  return (
    <DynamicBackground>
      <StyledContentContainer>
        {needToRequestUserName && <Intro />}
        {!needToRequestUserName && (
          <Fragment>
            <StyledTopContainer>
              {settings.isClockEnabled && (
                <StyledClockContainer>
                  <Clock format={ClockFormat['24Hours']} />
                </StyledClockContainer>
              )}

              {settings.isGreetingEnabled && (
                <StyledGreetingContainer>
                  <Greeting name={settings.isGreetingWithName ? name : undefined} />
                </StyledGreetingContainer>
              )}

              {settings.isSearchEnabled && (
                <StyledSearchContainer>
                  <Search />
                </StyledSearchContainer>
              )}

              {settings.isShortcutsEnabled && (
                <StyledShortcutsContainer>
                  <Shortcuts />
                </StyledShortcutsContainer>
              )}

              {settings.isToDoEnabled && (
                <StyledToDoContainer>
                  <InlineToDo />
                </StyledToDoContainer>
              )}
              <div style={{ display: 'block' }}>
                <ControlPanel />
              </div>
            </StyledTopContainer>

            <StyledBottomContainer>
              {settings.isQuoteEnabled && (
                <StyledQuoteContainer>
                  <Quote />
                </StyledQuoteContainer>
              )}
            </StyledBottomContainer>
          </Fragment>
        )}
      </StyledContentContainer>
    </DynamicBackground>
  );
}
