import { ComponentPropsWithoutRef, forwardRef } from 'react';

import {
  Size,
  StyledContainedButtonContent,
  StyledOutlinedButtonContent,
  StyledRoot,
  StyledTextButtonContent,
} from './Button.styles';

interface ButtonProps extends Partial<Size>, ComponentPropsWithoutRef<'button'> {
  variant?: 'outlined' | 'contained' | 'text';
}

const contentComponentsMap = {
  outlined: StyledOutlinedButtonContent,
  contained: StyledContainedButtonContent,
  text: StyledTextButtonContent,
};

export const Button = forwardRef<HTMLButtonElement, ButtonProps>(function Button(props, ref) {
  const { children, variant = 'outlined', size = 'medium', ...rest } = props;
  const StyledButtonContentComponent = contentComponentsMap[variant] || StyledContainedButtonContent;

  return (
    <StyledRoot {...rest} ref={ref} size={size}>
      <StyledButtonContentComponent size={size}>{children}</StyledButtonContentComponent>
    </StyledRoot>
  );
});
