import { SettingsModel } from '../models';
import { PersistedStorage } from '../services';

const defaultSettings: SettingsModel = {
  isClockEnabled: true,
  isGreetingEnabled: false,
  isGreetingWithName: false,
  isSearchEnabled: true,
  isShortcutsEnabled: true,
  isQuoteEnabled: true,
  isToDoEnabled: true,
};

export const settingsPersistedStorage = new PersistedStorage<SettingsModel>(
  'settings',
  // TODO should storages have a default value at all?
  // It makes the code harder to follow. Some storages have a default value, some don't have a default value, so
  // when two different storages are used, it's not clear in the place where they are used why for one storage
  // there is an explicit check for not found error but not for the other (there are more cases with similar issues).
  defaultSettings,
);
